/** color scheme */
.icon {
  width: 2em;
  height: 2em;
  border-radius: 50%;
  transform: translateY(0%);
  transition-property: transform;
  transition-duration: 0.2s;
  margin-right: 5px;
}
.text {
  font-size: 1.1em;
  transform: translateY(0%);
  transition-property: transform;
  transition-duration: 0.2s;
  line-height: 2em;
}
.item {
  position: relative;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 3px;
  margin: 5px;
  overflow: hidden;
  color: #171917;
  background-color: #A0D8EF;
  outline: none;
  padding: 5px;
}
.item:hover .icon,
.item:hover .text {
  transform: translateY(-150%);
}
.item:hover .delete,
.item:hover .add {
  transform: translateY(0%);
}
.delete,
.add {
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(150%);
  transition-property: transform;
  transition-duration: 0.2s;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  color: #FFFFFC;
  font-size: 1.5em;
}
.delete {
  background-color: #E198B4;
}
.add {
  background-color: #82AE46;
}



