/** color scheme */
.button {
  background-size: cover;
  background-position: center;
  background-color: initial;
  border: none;
  outline: none;
  font-size: 3em;
  height: 1.15em;
  width: 1.15em;
  text-align: center;
  color: #EC6D71;
}
.button.open {
  z-index: 2;
  animation: close 0.3s ease-out;
  animation-fill-mode: forwards;
  opacity: 1;
}
.button.close {
  z-index: 1;
  animation: open 0.3s ease-out;
  animation-fill-mode: forwards;
  opacity: 0;
}
.button i {
  display: block;
}
@keyframes open {
  0% {
    transform: rotateX(0deg);
    z-index: 2;
    opacity: 1;
  }
  50% {
    transform: rotateX(90deg);
    z-index: 1;
  }
  100% {
    transform: rotateX(0deg);
    z-index: 1;
    opacity: 0;
  }
}
@keyframes close {
  0% {
    transform: rotateX(0deg);
    z-index: 1;
    opacity: 0;
  }
  50% {
    transform: rotateX(90deg);
    z-index: 2;
  }
  100% {
    transform: rotateX(0deg);
    z-index: 2;
    opacity: 1;
  }
}



