/** color scheme */
.container {
  background-color: #171917;
  position: absolute;
  z-index: 99999;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  animation: slideup 0.25s ease-out;
  animation-fill-mode: forwards;
}
.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50px, -50px);
}
.circle {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  position: absolute;
  border-width: 1px;
  border-style: solid;
  animation: roll 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-delay: 0s;
  box-sizing: border-box;
}
.icon {
  width: 60px;
  height: 60px;
  background-image: url(../images/logo.png);
  background-size: cover;
  margin: 20px;
  animation: flag 5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
}
.left {
  border-color: #F4B3C2;
  border-bottom: none;
  border-right: none;
  border-top: none;
}
.right {
  border-color: #F4B3C2;
  border-bottom: none;
  border-left: none;
  border-top: none;
}
.top {
  border-color: #EC6D71;
  border-right: none;
  border-left: none;
  border-bottom: none;
}
.bottom {
  border-color: #FFFFFC;
  border-right: none;
  border-left: none;
  border-top: none;
}
.scale {
  animation: scale 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-delay: 0s;
}
.scale2 {
  animation: scale2 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-delay: 0s;
}
@keyframes flag {
  0% {
    opacity: 0;
    transform: scale(0.2);
  }
  50% {
    opacity: 1;
    transform: scale(0.75);
  }
  100% {
    opacity: 0;
    transform: scale(0.2);
  }
}
@keyframes roll {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes slideup {
  0% {
    opacity: 0.2;
  }
  100% {
    opacity: 0.8;
  }
}
@keyframes scale {
  0% {
    opacity: 0.2;
    transform: scale(0) rotate(0deg);
  }
  75% {
    opacity: 1;
    transform: scale(1) rotate(180deg);
  }
  100% {
    opacity: 0;
    transform: scale(1.5) rotate(360deg);
  }
}
@keyframes scale2 {
  0% {
    opacity: 0;
    transform: scale(0) rotate(0deg);
  }
  30% {
    opacity: 0.2;
    transform: scale(0) rotate(0deg);
  }
  75% {
    opacity: 1;
    transform: scale(1) rotate(180deg);
  }
  100% {
    opacity: 0;
    transform: scale(1.25) rotate(360deg);
  }
}



