/** color scheme */
@keyframes flip {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(180deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}
@keyframes flip-revert {
  0% {
    transform: rotateX(0deg);
  }
  50% {
    transform: rotateX(180deg);
  }
  100% {
    transform: rotateX(360deg);
  }
}
@keyframes to-backward {
  0% {
    z-index: 1;
  }
  50% {
    z-index: 0;
  }
}
@keyframes to-forward {
  0% {
    z-index: 0;
  }
  50% {
    z-index: 1;
  }
}
@keyframes push {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.8);
  }
  75% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes push-revert {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.8);
  }
  75% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes loading-with-middle {
  0% {
    transform: translateY(-50%) rotate(0deg);
  }
  50% {
    transform: translateY(-50%) rotate(180deg);
  }
  100% {
    transform: translateY(-50%) rotate(360deg);
  }
}
@keyframes finished-with-middle {
  0% {
    transform: translateY(-50%) rotate(0deg) scale(1);
  }
  25% {
    transform: translateY(-50%) rotate(90deg) scale(0);
  }
  26% {
    transform: translateY(-50%) scale(0);
  }
  90% {
    transform: translateY(-50%) scale(1.3);
  }
  100% {
    transform: translateY(-50%) scale(1);
  }
}
@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes finished {
  0% {
    transform: rotate(0deg) scale(1);
  }
  25% {
    transform: rotate(90deg) scale(0);
  }
  26% {
    transform: scale(0);
  }
  90% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes success-container {
  26% {
    color: #FFFFFC;
    background-color: #AACF53;
  }
  100% {
    background-color: #FFFFFC;
    color: #7D7D7D;
  }
}
@keyframes success {
  0% {
    content: '\f110';
  }
  25% {
    content: '\f110';
  }
  26% {
    content: '\f058';
    color: #AACF53;
  }
  100% {
    content: '\f058';
    color: #AACF53;
  }
}
@keyframes error-container {
  26% {
    color: #FFFFFC;
    background-color: #EC6D71;
  }
  100% {
    background-color: #FFFFFC;
    color: #7D7D7D;
  }
}
@keyframes error {
  0% {
    content: '\f110';
  }
  25% {
    content: '\f110';
  }
  26% {
    content: '\f071';
    color: #EC6D71;
  }
  100% {
    content: '\f071';
    color: #EC6D71;
  }
}
@keyframes opened {
  from {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
  }
}
@keyframes closed {
  from {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
  }
  to {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
  }
}
.progress-loading i {
  animation: loading 1s linear;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}
.progress-loading-with-middle i {
  animation: loading-with-middle 1s linear;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}
.progress-loading i:before,
.progress-loading-with-middle i:before {
  content: "\f110";
}
.progress-success-with-middle i,
.progress-error-with-middle i {
  animation: finished-with-middle 0.5s ease-in-out;
  animation-fill-mode: forwards;
}
.progress-success i,
.progress-error i {
  animation: finished 0.5s ease-in-out;
  animation-fill-mode: forwards;
}
.progress-success .button,
.progress-success-with-middle .input {
  animation: success-container 0.5s ease-in-out;
  animation-fill-mode: forwards;
}
.progress-success i:before,
.progress-success-with-middle i:before {
  animation: success 0.5s ease-in-out;
  animation-fill-mode: forwards;
}
.progress-error .button,
.progress-error-with-middle .input {
  animation: error-container 0.5s ease-in-out;
  animation-fill-mode: forwards;
}
.progress-error i:before,
.progress-error-with-middle i:before {
  animation: error 0.5s ease-in-out;
  animation-fill-mode: forwards;
}
.container {
  position: relative;
  width: 100%;
  padding: 1.75em;
  height: auto;
  padding-right: 0;
  padding-left: 0;
}
.input {
  position: absolute;
  color: transparent;
  height: 2.75em;
  border-style: solid;
  border-color: #FFFFFC;
  border-radius: 3px;
  border-width: 2px;
  background-color: transparent;
  top: 0;
  left: 0;
  width: 2.75em;
  z-index: 0;
  box-sizing: border-box;
  transition-property: border;
  transition-duration: 0.4s;
  outline: none;
  font-size: 1.25em;
  cursor: pointer;
}
.prefix {
  position: absolute;
  top: 50%;
  left: 0;
  z-index: 0;
  color: #FFFFFC;
  margin-left: 0.9em;
  transition-property: transform;
  transition-duration: 0.4s;
  font-size: 1.25em;
  transform: translateY(-50%) rotateX(0deg);
  cursor: pointer;
}
.input::-webkit-input-placeholder,
.input:-ms-input-placeholder,
.input::-moz-placeholder {
  color: #7D7D7D;
}
.input:focus {
  border-color: #7D7D7D;
  transition-property: border, transform;
  transition-duration: 0.4s;
  padding-left: 2.5em;
  background-color: rgba(0, 0, 0, 0.3);
  cursor: text;
  color: #EC6D71;
  width: 100%;
}
.input:focus,
.prefix {
  z-index: 1;
}
.input:focus + .prefix {
  color: #7D7D7D;
  transform: translateY(-50%) rotateX(360deg);
  cursor: text;
}



