/** color scheme */
.page {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  font-family: 'Tangerine', "Hiragino Kaku Gothic ProN", "", sans-serif;
  transition-duration: 0.2s;
  transition-property: right;
  z-index: 3;
  background-color: #171917;
  color: #FFFFFC;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.page.open {
  left: 0;
}
.page.close {
  left: -100vw;
}
.header {
  position: fixed;
  width: 100%;
  background-color: #171917;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  z-index: 3;
  transform: translate3d(0, 0, 0);
}
.closeButton {
  position: absolute;
  left: 5%;
}
.lead {
  text-align: center;
  font-size: 3em;
  font-weight: normal;
  margin: 0;
  margin-top: 0.15em;
  line-height: 1em;
}
.contents {
  margin-top: 4.5em;
}
@media (max-width: 568px) {
  .lead {
    font-size: 2em;
    margin-top: 0.25em;
  }
  .closeButton {
    top: 0;
  }
}



